* {
	word-wrap: break-word;
	word-break: break-all;
}


/*去掉内外边距-内外边距通常让各个浏览器样式的表现位置不同*/

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
iframe,
dl,
dt,
dd,
ul,
ol,
li,
pre,
form,
button,
input,
textarea,
th,
td,
fieldset {
	margin: 0;
	padding: 0
}


/*去掉列表标签的默认样式*/

ul,
ol,
dl {
	list-style-type: none
}


/*防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */

html {
	font-size: 100%;
	/*所有的px尺寸/100*/
}

address,
caption,
cite,
code,
dfn,
em,
th,
var {
	font-style: normal;
	font-weight: normal
}

input,
button,
textarea,
select,
optgroup,
option {
	font-family: inherit;
	font-size: inherit;
	font-style: inherit;
	font-weight: inherit
}

button {
	overflow: visible;
	vertical-align: middle;
	outline: none
}
