img {
	border: none;
}


/*---------------------HTML5 reset--------------------------*/

header,
footer,
section,
aside,
details,
menu,
article,
section,
nav,
address,
hgroup,
figure,
figcaption,
legend {
	display: block;
	margin: 0;
	padding: 0
}

time {
	display: inline
}

audio,
canvas,
video {
	display: inline-block;
	*display: inline;
	*zoom: 1
}

audio:not([controls]) {
	display: none
}

svg:not(:root) {
	overflow: hidden
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto
}

input[type="search"] {
	-webkit-appearance: textfield;
	/* 1 */
	-moz-box-sizing: content-box;
	-webkit-box-sizing: content-box;
	/* 2 */
	box-sizing: content-box
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none
}


/* HTML5 媒体文件跟 img 保持一致 */

audio,
canvas,
video {
	display: inline-block;
	*display: inline;
	*zoom: 1;
}


/*HTML5新模块元素在IE8、9版本浏览器中没有被定义默认样式。为解决该问题，给下列元素添加“block”显示属性*/

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
	display: block;
}

a,
p,
button,
i {
	-webkit-tap-highlight-color: transparent;
}


/*浮动*/

.fl {
	float: left;
	display: block;
}

.fr {
	float: right;
	display: block;
}


/*清除浮动*/

.clearfix {
	*zoom: 1;
}


/*解决ie6/7下面的浮动塌陷问题*/

.clearfix:before,
.clearfix:after {
	display: table;
	line-height: 0;
	content: "";
}


/*display:table;解决其他下面的浮动塌陷问题,line-height是将添加的内容高度设为0；*/

.clearfix:after {
	clear: both;
}

.middle:after {
	display: inline-block;
	content: "";
	height: 100%;
	vertical-align: middle;
}

.hidden {
	display: none;
}

.wrap {
	height: 100%;
}

.container {
	background: #f8f9fb;
}

a {
	color: inherit;
	transition: none;
}
textarea {
	/*overflow-y:hidden;*/
}
input,textarea{
   scrollbar-3dlight-color:transparent; 
   scrollbar-highlight-color:transparent; 
   scrollbar-face-color:transparent; 
   scrollbar-arrow-color:transparent; 
   scrollbar-shadow-color:transparent; 
   scrollbar-darkshadow-color:transparent; 
   scrollbar-base-color:transparent; 
   scrollbar-track-color:transparent;
}
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: transparent;
}
::-webkit-scrollbar-thumb {
    border-radius: 6px;
    background-color: rgba(151, 157, 190, 0.3);
    width: 6px;
    height: 6px;
}
::-webkit-scrollbar-track {
    border-radius: 6px;
    background-color: transparent;
    height: 6px;
    width: 6px;
}
::-ms-clear, ::-ms-reveal{display: none;}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
	color: #D0D0D0;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
	color: #D0D0D0;
}
input::placeholder,textarea::placeholder {
	color: #D0D0D0;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
	color: #D0D0D0;
}
.el-input__inner:-webkit-input-placeholder,.el-textarea__inner:-webkit-input-placeholder{
	color: #D0D0D0;
}
.el-input__inner:-moz-placeholder,.el-textarea__inner:-moz-placeholder{
	color: #D0D0D0;
}
.el-input__inner:-ms-input-placeholder,.el-textarea__inner:-ms-input-placeholder{
	color: #D0D0D0;
}
:-webkit-input-placeholder.el-input__inner,:-webkit-input-placeholder.el-textarea__inner{
	color: #D0D0D0;
}
:-moz-placeholder.el-input__inner,:-moz-placeholder.el-textarea__inner{
	color: #D0D0D0;
}
:-ms-input-placeholder.el-input__inner,:-ms-input-placeholder.el-textarea__inner{
	color: #D0D0D0;
}
.el-input__inner::placeholder,.el-textarea__inner::placeholder {
	color: #D0D0D0;
}


